body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.webtoolsQueryEditor {
  text-align: center;
  /*width: 500px;*/
  flex-grow: 1;
  padding: 5px;
}

.webtoolsQueryEditor .stack {
  display: block;
}

.webtoolsQueryEditor .paper {
    text-align: center,
}

.webtoolsQueryEditor .action-button {
  width: 100px;
}

.webtoolsQueryEditor .center {
  text-align: start;
}
.webtoolsQueryEditor .presetHeading {
  padding-left: 5px;
}

.webtoolsQueryEditor .presets {
  margin: 5px;
}

.webtoolsQueryEditor .presets:hover {
  background-color: lightgrey;
}

.webtoolsQueryEditor .hidden {
  display: none;
}

.webtoolsQueryEditor .audiences {
  padding: 5px;
}

.webtoolsQueryEditor .configPanel .key-column {
  /*flex-basis: 33.33%;*/
  width: 80px;
  text-align: start;
}
.webtoolsQueryEditor .configPanel .column {
  /*flex-basis: 33.33%;*/
  width: 150px;
}
.webtoolsQueryEditor .configPanel .description {
  color: darkgrey;
}

.webtoolsQueryEditor .catlist {
  list-style-type: none;
  padding-left: 5px;
}
.webtoolsQueryEditor .catlist .item {
  padding-left: 10px;
}
.webtoolsQueryEditor .tma_cats {
  text-align: start;
  max-height: 200px;
  width: 100%;
  /*overflow: scroll;*/
}

.webtoolsQueryEditor .tma_cats_condition {
  float: right; 
}
